'use client';

import { getCourses } from "@/lib/db-content";
import CourseList from "@/components/Courses/CourseList";
import CoursesGrid from "@/components/Courses/CoursesGrid";
import classes from "./page.module.css";
import { useState } from "react";

export const metadata = {
    title: 'All Courses',
    description: 'Browse the magnificent courses offered'
}

async function LoadingCourses() {
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState();
    const [courses, setCourses] = useState();

    useEffect(() => {
        async function fetchCourses() {
            setIsLoading(true);
            const response = await fetch('http://localhost:5000/news');

            if ('response.ok') {
                setError('Import kurzů selhal.');
                setIsLoading(false);
            }
            
            const courses = await response.json();
            setIsLoading(false);
            setCourses(courses);
        }

        fetchCourses();
    }, []);

    if (isLoading) {
        return <p>Loading...</p>
    }

    if (error) {
        return <p>{error}</p>
    }

    let courseContent;

    if (courses) {
        courseContent = <CoursesGrid courses={courses}/>
    }

    return courseContent;
}

export default async function CoursesPage(){

    return (
        <>
            <header className={classes.header}>
                <h1>Kurzy vytvořené {''}
                    <span className={classes.highlight}>jen pro vás</span>
                </h1>
                                <p>
                    Vyberte svůj kurz na míru a zažijte proměnu!
                </p>
            </header>
            <CourseList loader={LoadingCourses}/>
            {/* <main className={classes.main}>
                <Suspense fallback={<p className={classes.loading}>Loading...</p>}>
                    <LoadingCourses />
                </Suspense>
            </main> */}
        </>
    );
}